@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
.cmp-header{height:50px;display:flex;align-items:center;background-color:#1C91DA;padding:0 2vw}.cmp-header a{display:flex;align-items:center}.cmp-header a{color:beige;font-weight:bold;font-size:20PX;margin-right:20px}.cmp-header__nav{flex-basis:100%;display:flex;justify-content:space-between;align-content:center}.cmp-header__nav__profile{display:flex;align-content:center;justify-content:space-between}.cmp-header__nav__profile a{display:none}@media (min-width: 768px){.cmp-header__nav__profile a{display:flex}}.cmp-header__nav button{margin:0}

.cmp-footer{display:flex;justify-content:center;margin-top:50px;font-size:20px;font-weight:bold}

.cmp-home__content{display:flex;flex-wrap:wrap;align-items:center}.cmp-home__content--title,.cmp-home__content--paragraph{flex-basis:100%;text-align:center}.cmp-home__content__links{flex-basis:100%;text-align:center;justify-content:space-evenly;display:flex;margin-top:50px}.cmp-home__content__links--signin,.cmp-home__content__links--signup{padding:10px 15px;background-color:lightblue;border-radius:25px;color:#000}

.cmp-chat{margin:6vw auto 3vw;height:500px;max-width:1000px;overflow:auto;border:2px solid white;border-radius:25px;padding:2vw;background-color:#16202C}.cmp-chat--time{flex-basis:100%;font-style:italic;font-size:12px;text-align:left}.cmp-chat--item{flex-basis:100%;display:flex}.cmp-chat--item__content{max-width:180px;display:flex;flex-wrap:wrap;margin:10px 5px;color:white;justify-content:flex-start}.cmp-chat--item__content--paragraph{background-color:#3D5465;padding:10px 15px;width:auto;border-radius:25px 25px 25px 0;max-width:180px;word-wrap:break-word;display:block}@media (min-width: 768px){.cmp-chat--item__content{max-width:500px}}.cmp-chat--item.current-user{justify-content:flex-end}.cmp-chat--item.current-user .cmp-chat--time{text-align:right}.cmp-chat--item.current-user .cmp-chat--item__content{justify-content:flex-end}.cmp-chat--item.current-user .cmp-chat--item__content--paragraph{background-color:#1C91DA;border-radius:25px 25px 0}@media (min-width: 768px){.cmp-chat--item.current-user .cmp-chat--item__content--paragraph{max-width:500px}}.cmp-chat--form{display:flex;justify-content:center;align-items:center;flex-wrap:wrap}.cmp-chat--form input{flex-basis:100%;border-radius:16px;padding:5px;border:1px solid}@media (min-width: 768px){.cmp-chat--form input{flex-basis:500px}}.cmp-chat--form button{flex-basis:100%}@media (min-width: 768px){.cmp-chat--form button{flex-basis:unset}}

.cmp-loader{height:50%;left:0;position:absolute;width:100%}.cmp-loader .spinner{border:8px solid white;border-right-color:transparent;border-radius:50%;height:30px;left:50%;margin:-23px 0 0 -23px;position:absolute;top:50%;width:30px;animation-name:spin;animation-duration:1000ms;animation-iteration-count:infinite;animation-timing-function:linear}.cmp-loader .spinner.dark{border:8px solid black;border-right-color:transparent}@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.cmp-form__container{margin:0 2vw;display:flex;flex-wrap:wrap;justify-content:center}.cmp-form__container--title,.cmp-form__container--paragraph{flex-basis:100%;text-align:center}.cmp-form__container--title a{margin:0 10px}.cmp-form__container--input{padding:5px;border:0;border-bottom:1px solid}.cmp-form__container--submit{flex-basis:100%;text-align:center}.cmp-form__container hr{flex-basis:100%;margin:20px 100px}

body{font-family:'Poppins', sans-serif;background-color:#fcfcfc;margin:0;padding:0}a{text-decoration:none}button{padding:10px 50px;border:0;border-radius:25px;background-color:lightblue;margin:10px;cursor:pointer;font-size:16px;outline:none;transition:0.4s}button:hover{opacity:0.7}input{margin:20px;outline:none}

